<template>
	<view>
		<view class="status-bar" :style="{
			'height': (statusBarHeight - (-navHeight)) + 'px',
		}"></view>

		<view :style="{
			'height': (statusBarHeight - (-navHeight)) + 'px',
		}"></view>
		<view style="position: fixed;top: 0;left: 0;z-index: 99;width: 100%;">
			<logo-and-title></logo-and-title>
		</view>
		<view class="box">
			<avatarInit @change="avatarInitChange"></avatarInit>

			<otherInfoInit @emotionalOrientationOpen="emotionalOrientationOpen"
				@getByGenderAndSexualOrientationCodeFinally="getByGenderAndSexualOrientationCodeFinally"
				@change="otherInfoInitChange"></otherInfoInit>

			<view style="padding: 0 100rpx 0 100rpx;">
				<kt-button @click="toSubmit" ref="submitBtn">进入主页</kt-button>
			</view>
			{{tipText}}
			<view style="height: 60rpx;"></view>

		</view>
	</view>
</template>

<script>
	import avatarInit from "./inSelfInfoInit/avatarInit.vue"
	import otherInfoInit from "./inSelfInfoInit/otherInfoInit.vue"

	export default {
		components: {
			avatarInit,
			otherInfoInit
		},
		data() {
			let systemInfo = uni.getSystemInfoSync();
			return {
				statusBarHeight: systemInfo.statusBarHeight,
				componentOfShow: "otherInfoInit",
				navHeight: 48,
				avatarSrc: "",
				requestParam: {
					fileIdOfAvatar: "",
					nickname: "",
					birthday: "",
					gender: 0,
					introduction: "",
					sexualOrientationCode: "",
					sadomasochismAttrCode: [],
					emotionalOrientationCodeList: []
				},
				tipText: ""
			}
		},
		mounted() {
			this.navHeight = 44;
			// #ifdef MP-WEIXIN
			this.navHeight = uni.getSystemInfoSync().system.indexOf("ios") != -1 ? 44 : 48;
			// #endif

		},
		methods: {
			avatarInitChange(src) {
				// 将头像地址赋值
				this.avatarSrc = src;
			},
			otherInfoInitChange(requestParam) {
				// 将其他信息赋值
				this.requestParam.nickname = requestParam.nickname;
				this.requestParam.birthday = requestParam.birthday + "";
				this.requestParam.gender = requestParam.gender;
				this.requestParam.introduction = requestParam.introduction;
				this.requestParam.sexualOrientationCode = requestParam.sexualOrientationCode;
				this.requestParam.sadomasochismAttrCode = requestParam.sadomasochismAttrCode;
				this.requestParam.emotionalOrientationCodeList = requestParam.emotionalOrientationCodeList;
			},
			emotionalOrientationOpen() {
				this.$refs.submitBtn.loading();
			},
			getByGenderAndSexualOrientationCodeFinally() {
				this.$refs.submitBtn.toNone();
			},
			toSubmit() {
				this.$refs.submitBtn.loading();

				if (this.avatarSrc == "") {
					this.$refs.submitBtn.error("请上传头像");
					return;
				}
				if (this.requestParam.nickname == "") {
					this.$refs.submitBtn.error("请填写昵称");
					return;
				}

				if (this.requestParam.gender == "") {
					this.$refs.submitBtn.error("请选择性别");
					return;
				}

				if (this.requestParam.birthday == "") {
					this.$refs.submitBtn.error("请选择生日");
					return;
				}
				if (this.requestParam.sexualOrientationCode == "") {
					this.$refs.submitBtn.error("请选择性取向");
					return;
				}

				// if (this.requestParam.emotionalOrientationCodeList.length == 0) {
				// 	this.$refs.submitBtn.error("请选择情感取向");
				// 	return;
				// }

				if (this.requestParam.sadomasochismAttrCode == "") {
					this.$refs.submitBtn.error("请选择SM属性");
					return;
				}

				if (this.requestParam.introduction == "") {
					this.$refs.submitBtn.error("请填写自我介绍");
					return;
				}


				this.$kt.request.uploadFile({
					name: "file",
					formData: {
						groupCode: "avatar"
					},
					filePath: this.avatarSrc,
					success: (res) => {
						var json = JSON.parse(res.data);
						this.requestParam.fileIdOfAvatar = json.data.id;
						this.bindUser();
					},
					fail: (res) => {
						this.$refs.submitBtn.error("头像上传失败");
					}
				});
			},
			bindUser() {
				this.$kt.request.request({
					api: this.$api.business.ovo.userBind.bind,
					data: this.requestParam,
					stateSuccess: (res) => {
						this.$refs.submitBtn.success("信息完善成功");
						uni.$emit("loginSuccess", true);
						this.$ovo.setSelf(res.data);
						this.$kt.request.request({
							api: this.$api.system.balance.getSelfMap,
							stateSuccess: (res) => {
								this.$system.setBalanceMap(res.data);
								setTimeout(() => {
									uni.reLaunch({
										url: "/pages/body/static"
									});
								}, 200);

							}
						});
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.box {
		text-align: center;
		width: 100%;
		font-size: 40rpx;
	}

	.status-bar {
		background-color: #fff;
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 98;
	}
</style>
